@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700;900&display=swap);
.header {
  text-align: center;
  margin: 30px 0px;
  line-height: 1.4em;
}

.theme-info {
    font-size: 0.6em;
}

.github {
  height: 30px;
}

.toggle-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.toggle-slot {
  position: relative;
  height: 2em;
  width: 4em;
  border-radius: 2em;
  background-color: var(--toggle-color);
  box-shadow: -5px -5px 15px var(--card-shadow-color-top-left), 5px 5px 15px var(--card-shadow-color-bottom-right);
  transition: background-color .4s;
}

.toggle-slot:hover {
  cursor: pointer;
}

.toggle-checkbox:checked ~ .toggle-slot {
  background-color: var(--toggle-color);
}

.toggle-button {
  -webkit-transform: translate(2.4em, 0.35em);
          transform: translate(2.4em, 0.35em);
  position: absolute;
  height: 1.3em;
  width: 1.3em;
  border-radius: 50%;
  background-color: #ffeccf;
  box-shadow: inset 0px 0px 0px 1.5em #ffbb52;
  transition: background-color 250ms, border-color 250ms, -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: background-color 250ms, border-color 250ms, transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: background-color 250ms, border-color 250ms, transform 500ms cubic-bezier(.26,2,.46,.71), -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
}

.toggle-checkbox:checked ~ .toggle-slot .toggle-button {
  background-color: #485367;
  box-shadow: inset 0px 0px 0px 1.5em white;
  -webkit-transform: translate(0.3em, 0.35em);
          transform: translate(0.3em, 0.35em);
}

.sun-icon {
  position: absolute;
  height: 1.2em;
  width: 1.2em;
}

.sun-icon-wrapper {
  position: absolute;
  height: 1.2em;
  width: 1.2em;
  opacity: 1;
  -webkit-transform: translate(0em, .25em) rotate(15deg);
          transform: translate(0em, .25em) rotate(15deg);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: opacity 150ms, -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2,.46,.71), -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
}

.toggle-checkbox:checked ~ .toggle-slot .sun-icon-wrapper {
  opacity: 0;
  -webkit-transform: translate(1em, .25em) rotate(0deg);
          transform: translate(1em, .25em) rotate(0deg);
}

.moon-icon {
  position: absolute;
  height: 1.2em;
  width: 1.2em;
}

.moon-icon-wrapper {
  position: absolute;
  height: 1.2em;
  width: 1.2em;
  opacity: 0;
  -webkit-transform: translate(.8em, .55em) rotate(0deg);
          transform: translate(.8em, .55em) rotate(0deg);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: opacity 150ms, -webkit-transform 500ms cubic-bezier(.26,2.5,.46,.71);
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2.5,.46,.71);
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2.5,.46,.71), -webkit-transform 500ms cubic-bezier(.26,2.5,.46,.71);
}

.toggle-checkbox:checked ~ .toggle-slot .moon-icon-wrapper {
  opacity: 1;
  -webkit-transform: translate(1.8em, .55em) rotate(-15deg);
          transform: translate(1.8em, .55em) rotate(-15deg);
}
.search{
  background: var(--card-background-color);
  padding: 14px 22px;
  width: 100%;
  max-width: 500px;
  margin: auto;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  outline: none;
  box-shadow: -5px -5px 15px var(--card-shadow-color-top-left), 5px 5px 15px var(--card-shadow-color-bottom-right);
  border: none;
  font-weight: 500;
  border-radius: 6px;
  transition: 0.4s;
  cursor: pointer;
  box-sizing: border-box;
}

.search::-webkit-input-placeholder{
  color: #aaa;
}

.search:-ms-input-placeholder{
  color: #aaa;
}

.search::placeholder{
  color: #aaa;
}

.search:hover{
  background: var(--search-hover-color);
}

.search:focus{
  box-shadow: -3px -3px 6px var(--card-shadow-color-top-left), 3px 3px 6px var(--card-shadow-color-bottom-right), 0 0 0 3px var(--border-color);
  border-color: transparent;
  cursor: text;
}

.item{
  position: relative;
  width: 180px;
  height: 180px;
  background: var(--card-background-color);
  margin: 15px;
  padding: 20px;
  border-radius: 12px;
  box-shadow: -5px -5px 15px var(--card-shadow-color-top-left), 5px 5px 15px var(--card-shadow-color-bottom-right);
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.4s;
}

.item:hover{
  box-shadow: -5px -5px 15px var(--card-shadow-color-top-left), 5px 5px 15px var(--card-shadow-color-bottom-right), 0 0 0 5px var(--border-color);
}

.item-copy{
  position: absolute;
  background: #0077ff33;
  color: #0077ff;
  font-weight: 700;
  border-radius: 6px;
  font-size: 0.8em;
  padding: 6px;
  bottom: 8px;
  left: 8px;
  transition: opacity 0.3s;
  cursor: pointer;
}

.label-true{
  opacity: 1;
}

.label-false{
  opacity: 0;
}

.item-emoji{
  font-size: 2.4em;
  cursor: pointer;
}

.item-name{
  font-size: 0.9em;
  line-height: 1.4em;
  width: 150px;
  text-align: center;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .item{
    height: 120px;
    width: 130px;
  }
  .item-emoji{
    font-size: 1.6em;
  }
  .item-name{
    font-size: 0.8em;
    margin: 0px 20px;
    padding: 0px 20px;
    box-sizing: border-box;
  }
}

.results {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1em;
}

.no-result {
  margin: 3em 0;
  font-size: 0.8em;
  opacity: 0.7;
  text-align: center;
}

.Footer {
    text-align: left;
