  }
.scroll-to-top-wrapper {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
}

.scroll-top-button {
  padding: 1rem;
  border-radius: 1rem;
  background-color: var(--card-background-color);
  box-shadow: -5px -5px 15px var(--card-shadow-color-top-left), 5px 5px 15px var(--card-shadow-color-bottom-right);
  display: flex;
  align-items: center;
  justify-items: center;
  cursor: pointer;
  -webkit-animation: fadeIn 0.5s;
          animation: fadeIn 0.5s;
  transition: 0.4s;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: auto;
}

.first-render {
  margin: 4em 0;
  font-size: 0.8em;
  opacity: 0.7;
  text-align: center;
}

body {
  font-family: "Inter", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  min-height: 100vh;
  width: 100%;
  background-color: var(--background-color);
  transition: 0.3s;
  color: var(--text-color-first);
}

#root {
  max-width: 1000px;
  width: auto;
  margin: auto;
  padding: 20px;
}

input {
  color: var(--text-color-first);
  caret-color: var(--cursor-color);
  transition: 0.4s;
}



.light {
  --background-color: #eaedf0;
  --text-color-first: #000000;
  --card-background-color: #ffffff;
  --card-shadow-color-top-left: #ffffff99;
  --card-shadow-color-bottom-right: #152b4711;
  --border-color: #0077ff49;
  --search-hover-color: #f6f6f6;
  --cursor-color: #000;
  --toggle-color: #ffffff;
}

.dark {
  --background-color: #121212;
  --text-color-first: #b3b3b3;
  --card-background-color: #1d1d1d;
  --card-shadow-color-top-left: #00000099;
  --card-shadow-color-bottom-right: #96969611;
  --border-color: #005ac249;
  --search-hover-color: #ffffff09;
  --cursor-color: #fff;
  --toggle-color: #292929;
}
